import * as mui from '@mui/material';
import './Login.css';
import { useState } from "react"
import { Navigate, useNavigate } from "react-router-dom"
import config from "./config.json"

function Login() {
  const [userValue, setUserValue] = useState("")
  const [logs, setLogs] = useState(false)
  const navigate = useNavigate()

  async function postAuth(user) {
    let resp;
    try {
      resp = await fetch(`${config.login_host}/auth`, {
        method: 'POST',
        headers: {
          'Accept': 'application/json',
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          "username": user
        }),
      });
      if (resp.status == 401) {
        resp.json().then(function (result) {
          alert(result.message)
        })
      }
    } catch (e) { }
    return resp;
  }

  const post = () => {
    (async () => {
        postAuth(userValue).then(res => {if (res.status != 401) {
          res.json().then(function(result) {
            try {
              fetch(`${config.user_host}/users/` + result.token.user_id, {
                method: 'GET',
              }).then(res => res.json()).then(data => {
                let rA = data.read_access ? "read/" : ""
                let pA = data.post_access ? "post/" : ""
                let cA = data.comment_access ? "comment/" : ""
                let mA = data.media_access ? "media" : ""

              //let exp = new Date();
              //let time = exp.getTime();
              //time += 2* 60 * 60 * 1000; //Sets token to expire after 2 hours
              //exp.setTime(time);
              let tokenString = result.token.user_id + "/" + rA + pA + cA + mA
              document.cookie = 'token=' + tokenString + "; " +
                //'expires=' + exp.toUTCString() + "; "
                '; path=/';
              setLogs(true)
              })
            } catch(e) {alert(e)}
        })}})
    })();
  }

  if (logs) {
    navigate(0)
    return <Navigate replace to="/dashboard" />;
  } else {
    return (
      <div className="App">
        <mui.Box
          sx={{
            width: 400,
            height: 400,
            color: 'text.main',
            borderColor: 'primary.main',
            border: 2,
            borderRadius: 2,
            position: 'absolute',
            top: 160,
            left: '35%'
          }}>

          <div className="Title">
            Login
          </div>

          <div className="Username-label">
            <mui.TextField
              label="Username"
              id="username"
              placeholder='Type your username'
              variant="filled"
              size="small"
              style={{ maxWidth: '300px', maxHeight: '50px', minWidth: '300px', minHeight: '50px' }}
              onChange={(userValue) => setUserValue(userValue.target.value)}
            />
          </div>

          {/* <div className="Password-label">
						<mui.TextField
							label="Password"
							id="password"
							defaultValue="Type your password"
							variant="filled"
							size="small"
							style={{ maxWidth: '300px', maxHeight: '60px', minWidth: '300px', minHeight: '60px' }}
						/>
					</div> */}

          <div className="Submit">
            <mui.Button variant="contained" onClick={() => { post() }}
              style={{
                maxWidth: '300px', maxHeight: '30px', minWidth: '300px', minHeight: '30px',
                background: 'linear-gradient(to right bottom, #aa00aa, #3333aa)'
              }}>
              Login
            </mui.Button>
          </div>

          <div className="Link-out">
            <mui.Link href='/register'>Register</mui.Link>
          </div>

        </mui.Box>
      </div>
    );
  }
}

export default Login;
