import { useState } from "react";
import * as mui from '@mui/material';
import './Login.css';
import { Navigate, useNavigate } from "react-router-dom"
import config from "./config.json"

function Register() {
  const [userValue, setUserValue] = useState("")
  const [firstNameValue, setFirstValue] = useState("")
  const [lastNameValue, setLastValue] = useState("")
  const [login, setLogin] = useState(false)
  const navigate = useNavigate()

  function generate_guid() {
    let dt = new Date().getTime();
    return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g,
      function (c) {
        var rnd = Math.random() * 16;
        rnd = (dt + rnd) % 16 | 0;
        dt = Math.floor(dt / 16);
        return (c === 'x' ? rnd : (rnd & 0x3 | 0x8)).toString(16);
      });
  }

  async function postLogin(userID) {
    let resp;
    try {
      resp = await fetch(`${config.login_host}/login`, {
        method: 'POST',
        headers: {
          'Accept': 'application/json',
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          "user_id": userID,
          "username": userValue
        }),
      })
      if (resp.status == 404) {
        resp.json().then(function (result) {
          alert("ON postLogin()\n"+ result.message)
        })
      }
    } catch (e) { alert("ON postLogin()" + e.message) }
    return resp.status;
  }

  async function postAuth(user) {
    let resp;
    try {
      resp = await fetch(`${config.login_host}/auth`, {
        method: 'POST',
        headers: {
          'Accept': 'application/json',
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          "username": user
        }),
      });
      if (resp.status == 401) {
        resp.json().then(function (result) {
          alert("ON postAuth()\n"+result.message)
        })
      }
    } catch (e) { alert("ON postAuth()\n" + e.message)}
    return resp;
  }

  const postRequest = (userID) => {
    (async () => {
      try {
        await fetch(`${config.user_host}/users`, {
          method: 'POST',
          headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({
            "user_id": userID,
            "username": userValue,
            "first_name": firstNameValue,
            "last_name": lastNameValue,
            "post_ids": [],
            "comment_ids": [],
            "user_group": "normal"
          }),
        });
      } catch (e) {
        alert(e);
      }
    })();
  }

  const postRequestAdmin = (userID) => {
    (async () => {
      try {
        await fetch(`${config.user_host}/users`, {
          method: 'POST',
          headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({
            "user_id": userID,
            "username": userValue,
            "first_name": firstNameValue,
            "last_name": lastNameValue,
            "post_ids": [],
            "comment_ids": [],
            "user_group": "admin"  //Change between mod/admin for testing
          }),
        });
      } catch (e) {
        alert(e.message);
      }
    })();
  }

  const posts = () => {
    const new_userID = generate_guid();
    (async () => {
      postLogin(new_userID).then(res => {if (res!=404) {
        postRequest(new_userID) //Change back to admin request for groups testing

        postAuth(userValue).then(res => res.json().then(function(result) {
          try {
            fetch(`${config.user_host}/users/` + result.token.user_id, {
              method: 'GET',
            }).then(res => res.json()).then(data => {
              let rA = data.read_access ? "read/" : ""
              let pA = data.post_access ? "post/" : ""
              let cA = data.comment_access ? "comment/" : ""
              let mA = data.media_access ? "media" : ""

            //let exp = new Date();
            //let time = exp.getTime();
            //time += 2* 60 * 60 * 1000; //Sets token to expire after 2 hours
            //exp.setTime(time);
            let tokenString = result.token.user_id + "/" + rA + pA + cA + mA
            document.cookie = 'token=' + tokenString + "; " +
              //'expires=' + exp.toUTCString() + "; "
              '; path=/';
            setLogin(true)
            })
          } catch(e) {alert("ON posts()\n" + e.message)}
        }))
      }})

    })();
  }

  if (login) {
    navigate(0)
    return <Navigate replace to="/dashboard" />;
  } else {
    return (
      <div className="App">
        <mui.Box
          sx={{
            width: 400,
            height: 400,
            color: 'text.main',
            borderColor: 'primary.main',
            border: 2,
            borderRadius: 2,
            position: 'absolute',
            top: 160,
            left: '35%'
          }}>

          <div className="Title">
            Register
          </div>

          <div className="Name-label">
            <mui.TextField
              label="First Name"
              id="firstname"
              placeholder="Type your first name"
              variant="filled"
              size="small"
              style={{ maxWidth: '150px', maxHeight: '50px', minWidth: '150px', minHeight: '50px' }}
              onChange={(firstNameValue) => setFirstValue(firstNameValue.target.value)}
            />
            <mui.TextField
              label="Last Name"
              id="lastname"
              placeholder="Type your last name"
              variant="filled"
              size="small"
              style={{ maxWidth: '150px', maxHeight: '50px', minWidth: '150px', minHeight: '50px' }}
              onChange={(lastNameValue) => setLastValue(lastNameValue.target.value)}
            />
          </div>

          <div className="Username-label">
            <mui.TextField
              label="Username"
              id="username"
              placeholder="Type your username"
              variant="filled"
              size="small"
              style={{ maxWidth: '300px', maxHeight: '50px', minWidth: '300px', minHeight: '50px' }}
              onChange={(userValue) => setUserValue(userValue.target.value)}
            />
          </div>

          {/*
          <div className="Password-label">
            <mui.TextField
              label="Password"
              id="password"
              defaultValue="Type your password"
              variant="filled"
              size="small"
              style={{maxWidth: '300px', maxHeight: '60px', minWidth: '300px', minHeight: '60px'}}
            />
          </div>
          */}

          <div className="Submit">
            <mui.Button variant="contained" onClick={() => { posts() }}
              style={{
                maxWidth: '300px', maxHeight: '30px', minWidth: '300px', minHeight: '30px',
                background: 'linear-gradient(to right bottom, #aa00aa, #3333aa)'
              }}>
              Register
            </mui.Button>
          </div>

          <div className="Link-out">
            <mui.Link href='/login'>Already have an account? Login</mui.Link>
          </div>

        </mui.Box>
      </div>
    );
  }
}

export default Register;
